<template>
    <div name="userBody" id="userBody" class="user-body">
      <div class="user-header">
        <a class="nav-item"><img class="game_1" src="../../assets/images/usercenter/game_1.png"/></a>
        <a class="nav-item"></a>
        <a class="nav-item"><img class="wh_38" src="../../assets/images/usercenter/wh_38.png"/></a>
      </div>
    </div>
</template>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };

  .user-body{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    background-color: #f2f2f2;
  }
  .user-header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: n(80);
    background: #fff001;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 999;
    border-bottom: 1px solid #d8d8d8;
    .nav-item{
      .game_1{
        margin-left: n(23);
        width: n(50);
        height: n(50);
      }
      .wh_38{
        @extend .game_1;
        margin-right: n(23);
      }
    }
  }
</style>
<script>


    export default {
      name:'usercenter',
        data() {
            return {
            }
        },
    }
</script>



